___CSS_LOADER_EXPORT___.push([module.id, "\n#app-content > div[data-v-7a1157e0] {\n\twidth: 100%;\n\theight: 100%;\n\tpadding: 20px;\n\tdisplay: flex;\n\tflex-direction: column;\n\tflex-grow: 1;\n}\ninput[type='text'][data-v-7a1157e0] {\n\twidth: 100%;\n}\ntextarea[data-v-7a1157e0] {\n\tflex-grow: 1;\n\twidth: 100%;\n}\n", "",{"version":3,"sources":["webpack://src/App.vue"],"names":[],"mappings":";AAwNA;CACA,WAAA;CACA,YAAA;CACA,aAAA;CACA,aAAA;CACA,sBAAA;CACA,YAAA;AACA;AAEA;CACA,WAAA;AACA;AAEA;CACA,YAAA;CACA,WAAA;AACA","sourcesContent":["<template>\n\t<div id=\"content\" class=\"app-notestutorial\">\n\t\t<AppNavigation>\n\t\t\t<AppNavigationNew v-if=\"!loading\"\n\t\t\t\t:text=\"t('notestutorial', 'New note')\"\n\t\t\t\t:disabled=\"false\"\n\t\t\t\tbutton-id=\"new-notestutorial-button\"\n\t\t\t\tbutton-class=\"icon-add\"\n\t\t\t\t@click=\"newNote\" />\n\t\t\t<ul>\n\t\t\t\t<AppNavigationItem v-for=\"note in notes\"\n\t\t\t\t\t:key=\"note.id\"\n\t\t\t\t\t:title=\"note.title ? note.title : t('notestutorial', 'New note')\"\n\t\t\t\t\t:class=\"{active: currentNoteId === note.id}\"\n\t\t\t\t\t@click=\"openNote(note)\">\n\t\t\t\t\t<template slot=\"actions\">\n\t\t\t\t\t\t<ActionButton v-if=\"note.id === -1\"\n\t\t\t\t\t\t\ticon=\"icon-close\"\n\t\t\t\t\t\t\t@click=\"cancelNewNote(note)\">\n\t\t\t\t\t\t\t{{ t('notestutorial', 'Cancel note creation') }}\n\t\t\t\t\t\t</ActionButton>\n\t\t\t\t\t\t<ActionButton v-else\n\t\t\t\t\t\t\ticon=\"icon-delete\"\n\t\t\t\t\t\t\t@click=\"deleteNote(note)\">\n\t\t\t\t\t\t\t{{ t('notestutorial', 'Delete note') }}\n\t\t\t\t\t\t</ActionButton>\n\t\t\t\t\t</template>\n\t\t\t\t</AppNavigationItem>\n\t\t\t</ul>\n\t\t</AppNavigation>\n\t\t<AppContent>\n\t\t\t<div v-if=\"currentNote\">\n\t\t\t\t<input ref=\"title\"\n\t\t\t\t\tv-model=\"currentNote.title\"\n\t\t\t\t\ttype=\"text\"\n\t\t\t\t\t:disabled=\"updating\">\n\t\t\t\t<textarea ref=\"content\" v-model=\"currentNote.content\" :disabled=\"updating\" />\n\t\t\t\t<input type=\"button\"\n\t\t\t\t\tclass=\"primary\"\n\t\t\t\t\t:value=\"t('notestutorial', 'Save')\"\n\t\t\t\t\t:disabled=\"updating || !savePossible\"\n\t\t\t\t\t@click=\"saveNote\">\n\t\t\t</div>\n\t\t\t<div v-else id=\"emptycontent\">\n\t\t\t\t<div class=\"icon-file\" />\n\t\t\t\t<h2>{{ t('notestutorial', 'Create a note to get started') }}</h2>\n\t\t\t</div>\n\t\t</AppContent>\n\t</div>\n</template>\n\n<script>\nimport ActionButton from '@nextcloud/vue/dist/Components/ActionButton'\nimport AppContent from '@nextcloud/vue/dist/Components/AppContent'\nimport AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'\nimport AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem'\nimport AppNavigationNew from '@nextcloud/vue/dist/Components/AppNavigationNew'\n\nimport '@nextcloud/dialogs/styles/toast.scss'\nimport { generateUrl } from '@nextcloud/router'\nimport { showError, showSuccess } from '@nextcloud/dialogs'\nimport axios from '@nextcloud/axios'\n\nexport default {\n\tname: 'App',\n\tcomponents: {\n\t\tActionButton,\n\t\tAppContent,\n\t\tAppNavigation,\n\t\tAppNavigationItem,\n\t\tAppNavigationNew,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tnotes: [],\n\t\t\tcurrentNoteId: null,\n\t\t\tupdating: false,\n\t\t\tloading: true,\n\t\t}\n\t},\n\tcomputed: {\n\t\t/**\n\t\t * Return the currently selected note object\n\t\t * @returns {Object|null}\n\t\t */\n\t\tcurrentNote() {\n\t\t\tif (this.currentNoteId === null) {\n\t\t\t\treturn null\n\t\t\t}\n\t\t\treturn this.notes.find((note) => note.id === this.currentNoteId)\n\t\t},\n\n\t\t/**\n\t\t * Returns true if a note is selected and its title is not empty\n\t\t * @returns {Boolean}\n\t\t */\n\t\tsavePossible() {\n\t\t\treturn this.currentNote && this.currentNote.title !== ''\n\t\t},\n\t},\n\t/**\n\t * Fetch list of notes when the component is loaded\n\t */\n\tasync mounted() {\n\t\ttry {\n\t\t\tconst response = await axios.get(generateUrl('/apps/notestutorial/notes'))\n\t\t\tthis.notes = response.data\n\t\t} catch (e) {\n\t\t\tconsole.error(e)\n\t\t\tshowError(t('notestutorial', 'Could not fetch notes'))\n\t\t}\n\t\tthis.loading = false\n\t},\n\n\tmethods: {\n\t\t/**\n\t\t * Create a new note and focus the note content field automatically\n\t\t * @param {Object} note Note object\n\t\t */\n\t\topenNote(note) {\n\t\t\tif (this.updating) {\n\t\t\t\treturn\n\t\t\t}\n\t\t\tthis.currentNoteId = note.id\n\t\t\tthis.$nextTick(() => {\n\t\t\t\tthis.$refs.content.focus()\n\t\t\t})\n\t\t},\n\t\t/**\n\t\t * Action tiggered when clicking the save button\n\t\t * create a new note or save\n\t\t */\n\t\tsaveNote() {\n\t\t\tif (this.currentNoteId === -1) {\n\t\t\t\tthis.createNote(this.currentNote)\n\t\t\t} else {\n\t\t\t\tthis.updateNote(this.currentNote)\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t * Create a new note and focus the note content field automatically\n\t\t * The note is not yet saved, therefore an id of -1 is used until it\n\t\t * has been persisted in the backend\n\t\t */\n\t\tnewNote() {\n\t\t\tif (this.currentNoteId !== -1) {\n\t\t\t\tthis.currentNoteId = -1\n\t\t\t\tthis.notes.push({\n\t\t\t\t\tid: -1,\n\t\t\t\t\ttitle: '',\n\t\t\t\t\tcontent: '',\n\t\t\t\t})\n\t\t\t\tthis.$nextTick(() => {\n\t\t\t\t\tthis.$refs.title.focus()\n\t\t\t\t})\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t * Abort creating a new note\n\t\t */\n\t\tcancelNewNote() {\n\t\t\tthis.notes.splice(this.notes.findIndex((note) => note.id === -1), 1)\n\t\t\tthis.currentNoteId = null\n\t\t},\n\t\t/**\n\t\t * Create a new note by sending the information to the server\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync createNote(note) {\n\t\t\tthis.updating = true\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(generateUrl('/apps/notestutorial/notes'), note)\n\t\t\t\tconst index = this.notes.findIndex((match) => match.id === this.currentNoteId)\n\t\t\t\tthis.$set(this.notes, index, response.data)\n\t\t\t\tthis.currentNoteId = response.data.id\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('notestutorial', 'Could not create the note'))\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\t\t/**\n\t\t * Update an existing note on the server\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync updateNote(note) {\n\t\t\tthis.updating = true\n\t\t\ttry {\n\t\t\t\tawait axios.put(generateUrl(`/apps/notestutorial/notes/${note.id}`), note)\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('notestutorial', 'Could not update the note'))\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\t\t/**\n\t\t * Delete a note, remove it from the frontend and show a hint\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync deleteNote(note) {\n\t\t\ttry {\n\t\t\t\tawait axios.delete(generateUrl(`/apps/notestutorial/notes/${note.id}`))\n\t\t\t\tthis.notes.splice(this.notes.indexOf(note), 1)\n\t\t\t\tif (this.currentNoteId === note.id) {\n\t\t\t\t\tthis.currentNoteId = null\n\t\t\t\t}\n\t\t\t\tshowSuccess(t('notestutorial', 'Note deleted'))\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('notestutorial', 'Could not delete the note'))\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\t#app-content > div {\n\t\twidth: 100%;\n\t\theight: 100%;\n\t\tpadding: 20px;\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\tflex-grow: 1;\n\t}\n\n\tinput[type='text'] {\n\t\twidth: 100%;\n\t}\n\n\ttextarea {\n\t\tflex-grow: 1;\n\t\twidth: 100%;\n\t}\n</style>\n"],"sourceRoot":""}]);
